$red: red;
$green: green;
$yel-main: #F9B233;
$blue-main: #1A1D3A;

html, body {
    width: 100%;
    // height: 100%
    display: flex;
    min-height: 100vh;
    flex-direction: column;
    // @media screen and (max-width: 1150px)
 }    //  height: auto
body {
    font-family: 'Source Sans Pro', sans-serif;
    // min-width: 1175px
    // display: table
 }    // display: block
a {
    text-decoration: none;
    &:focus {
        outline: none; } }
li:focus {
    outline: none; }
select, input, textarea {
    border: 0;
    border-radius: 0;
    box-shadow: none;
    outline: none;
    border-style: none;
    background: 0;

    box-shadow: none;
    -webkit-appearance: none;
    -moz-appearance: none; }

@mixin positions {
    position: relative;
    display: block;
    width: 100%; }
